import { Header } from "@/components/header";
import { Footer } from "@/components/footer";

export default function PrivacyPage() {
  return (
    <div className="min-h-screen flex flex-col bg-gradient-to-br from-purple-950 via-purple-900 to-indigo-900 text-white">
      <Header />

      <main className="flex-grow px-6 sm:px-10 lg:px-24 py-16">
        <div className="max-w-4xl mx-auto">
          {/* Заголовок */}
          <div className="text-center mb-12">
            <h1 className="text-4xl font-bold mb-4">Политика конфиденциальности</h1>
            <p className="text-gray-300 text-lg">
              Последнее обновление: <span className="font-medium">01 ноября 2025</span>
            </p>
          </div>

          {/* Карточка с контентом */}
          <div className="bg-white/10 backdrop-blur-lg rounded-2xl shadow-xl p-8 space-y-8 border border-white/20">
            {/* 1 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">1. Какие данные мы собираем</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>1.1.</strong> Данные для регистрации и идентификации: имя, фамилия, email, номер телефона, платёжные реквизиты.</li>
                <li><strong>1.2.</strong> Технические данные: IP-адрес, логи доступа, cookies и другие технологии для анализа работы сайта.</li>
                <li><strong>1.3.</strong> Данные, которые вы храните на сервере. Мы не просматриваем их, за исключением случаев, предусмотренных законом или нарушением условий использования.</li>
              </ul>
            </section>

            {/* 2 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">2. Цели сбора и обработки данных</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>2.1.</strong> Предоставление, управление и поддержка услуг.</li>
                <li><strong>2.2.</strong> Обработка платежей и связь с вами по поводу аккаунта.</li>
                <li><strong>2.3.</strong> Отправка уведомлений об изменениях услуг, тарифов и технических работах.</li>
                <li><strong>2.4.</strong> Улучшение качества сервиса и разработка новых продуктов.</li>
              </ul>
            </section>

            {/* 3 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">3. Передача данных третьим лицам</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>3.1.</strong> Мы не продаём персональные данные третьим лицам.</li>
                <li><strong>3.2.</strong> Мы можем передавать данные:
                  <ul className="ml-6 list-disc list-inside space-y-1">
                    <li><strong>Партнёрам и подрядчикам:</strong> для обработки платежей и рассылок при соблюдении конфиденциальности.</li>
                    <li><strong>По требованию закона:</strong> государственным органам в установленных случаях.</li>
                  </ul>
                </li>
              </ul>
            </section>

            {/* 4 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">4. Защита данных</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>4.1.</strong> Мы используем технические и организационные меры для защиты данных.</li>
                <li><strong>4.2.</strong> Доступ к серверам ограничен строго определённым кругом лиц.</li>
              </ul>
            </section>

            {/* 5 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">5. Хранение данных</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>5.1.</strong> Данные хранятся до тех пор, пока это необходимо или пока вы не отзовёте согласие.</li>
                <li><strong>5.2.</strong> После удаления аккаунта данные аннулируются, за исключением случаев, когда закон требует их сохранения.</li>
              </ul>
            </section>

            {/* 6 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">6. Ваши права</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>6.1.</strong> Вы можете запросить доступ, исправление или удаление данных.</li>
                <li><strong>6.2.</strong> Вы можете отказаться от маркетинговых рассылок в любое время.</li>
              </ul>
            </section>

            {/* 7 */}
            <section>
              <h2 className="text-2xl font-semibold mb-3">7. Согласие и изменения</h2>
              <ul className="space-y-2 text-gray-200 leading-relaxed">
                <li><strong>7.1.</strong> Используя наш сервис, вы соглашаетесь с условиями данной политики.</li>
                <li><strong>7.2.</strong> Мы можем обновлять Политику; актуальная версия всегда доступна на сайте.</li>
              </ul>
            </section>
          </div>
        </div>
      </main>

      <Footer />
    </div>
  );
}
